@import url(https://fonts.googleapis.com/css?family=Josefin+Sans);
@import url(https://fonts.googleapis.com/css2?family=Lora:ital@1&display=swap" rel="stylesheet);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.logo{
    font-family: 'Josefin Sans', cursive;

    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 10px;
    font-size: 1em;
}

.bars{
    display: none;
    padding: 20px;
}
@media only screen  and (max-width:1000px){
.logo{
    font-size: .7em;
}
.bars{
    display: block
}

}
.nav-list li:hover{
    border-bottom: 2px solid orange;
}
.nav-list{
    display: flex;
    font-size: 1em;
    align-items: center;
    justify-content: space-evenly;
    transition: 2s;

}
.nav-list li,.show li{
    list-style: none;
    cursor: pointer;
    padding: 10px;
    border: none;
    font-family: 'Lora', serif;

    
}
@media only screen  and (max-width:1000px){
.nav-list{
    display: block;
    height: 0;
    overflow: hidden;
}
.show{
    display: block;
    height: 180px;
    overflow: hidden;
    transition: 2s;

}
.show li:hover{
    border: none;
    background-color: rgb(211, 211, 211);
}
}

    .search-bar{
        display: flex;
        align-items: center;
        padding: 10px;
    }
    .search-bar input{
        
    padding: 10px;
    border: solid 2px black;
    width: 20vw;
    margin-right: 10px;
    
    }

    @media only screen  and (max-width:1000px){
        .search-bar input{
            width: 85vw;
            
        }

    }


.youtube{
    color: red;
}
.instagram{
    color: rgb(255, 100, 126);

}
.facebook{
    color: rgb(5, 5, 66);

}
.contact{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.contact li{
    cursor: pointer;
    font-size: 1.2em;
    padding: 20px;
    list-style: none;
}

@media only screen  and (max-width:1000px){
    .contact{
        display: none;
    }
    
    }
.navbar
{
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 20px;
}
@media only screen  and (max-width:1001px){
    .navbar{
        display: block;
    }
    }

